import { Button, Container, Typography } from '@material-ui/core';
import { Link, useHistory, useParams } from 'react-router-dom';

function PoistaTreeni(props) {

    const history = useHistory();
    const {id} = useParams();

    const poistettavaTreeni = props.treenit.filter((treeni) => {
                                return (treeni.id === id);
                            })[0];

    const treeninPoisto = (e) => {
        e.preventDefault();

        let treenitApu = props.treenit.filter((treeni) => {
            return (treeni.id !== id);
        });
        
        props.setTreenit([...treenitApu]);
        history.push("/");
    }

    return (
        <Container maxWidth="sm"> 
            
            <form onSubmit={treeninPoisto}>

            <Typography style={{marginTop: "20px", textAlign: "center"}}>Vahvista harjoituksen poisto:</Typography> 

            <Typography style={{margin: "10px 0 20px 0", textAlign: "center"}} >Haluatko varmasti poistaa harjoituksen {poistettavaTreeni.nimi}?</Typography>

            <Button 
                type="submit"
                variant="contained"
                fullWidth
                style={{marginBottom : 10, backgroundColor: "#a775f3"}}
            >Poista harjoitus</Button>

            <Button 
                component={Link}
                to="/"
                variant="contained"
                style={{backgroundColor: "#b09dd8"}}
                fullWidth
            >Peruuta</Button>

            </form>
        
        </Container>
    )
}

export default PoistaTreeni;