import { Container, List, Typography, ListItemText, ListItem, ListItemIcon, IconButton, Badge, Button } from "@material-ui/core";
import { Link } from 'react-router-dom';
import { format, differenceInDays } from 'date-fns';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import DoneOutlineIcon from '@material-ui/icons/DoneOutline';

function Treenilista(props) {

    const suoritettu = (indeksi) => {

        let treenitApu = [...props.treenit];
        treenitApu[indeksi].suoritettu = !treenitApu[indeksi].suoritettu;
        props.setTreenit([...treenitApu]);
  
    }

    return (
        <Container maxWidth="sm">
            <Typography variant="h5" style={{textAlign: "center", marginTop: "20px", marginBottom: "10px"}}>Tervetuloa harjoitussuunnitelmaohjelmistoon!</Typography>
            <Typography style={{textAlign: "center", marginBottom: "10px"}}>Pistetään treenit käyntiin hyvällä sykkeellä!</Typography>
            
            <Button
                component={Link}
                to="/lisaa"
                variant="contained"
                color="primary"
                fullWidth
                size="large"
                style={{marginBottom : 10, backgroundColor: "#a775f3"}}
                >Lisää uusi harjoitussuunnitelma
            </Button>

            <List>
                { props.treenit.map((treeni, idx) => {
  
                return (
                    <ListItem key={idx}>
                        <ListItemIcon>
                          <IconButton
                            onClick={() => {suoritettu(idx)}}
                          >
                            {(treeni.suoritettu)
                                ? <DoneOutlineIcon color="primary" />
                                : (differenceInDays(treeni.aika, new Date()) <= 0 )
                                    ? <Badge badgeContent=" " variant="dot" color="error">
                                        <CheckBoxOutlineBlankIcon />
                                      </Badge>
                                    : <CheckBoxOutlineBlankIcon />
                            } 
                          </IconButton>
                      </ListItemIcon>
                        <ListItemText primary={`Harjoitus: ${treeni.nimi}  (Oletuskesto: ${treeni.kesto} minuuttia)`} secondary={format(treeni.aika, "d.M.y HH:mm")}/>
                        <ListItemIcon>
                        <IconButton 
                          edge="end"
                          color="primary"
                          component={Link}
                          to={`/poistatreeni/${treeni.id}`} 
                        >
                            <HighlightOffIcon />
                        </IconButton>
                        </ListItemIcon>
                    </ListItem>
                );
                } ) }
            </List>

            <Typography style={{color: "purple", textAlign: "center"}}>Jos harjoituksen suoritusajankohta on seuraavan 24 tunnin sisällä, <br/> ilmestyy harjoituksen yhteyteen punainen merkki!</Typography>

        </Container>
    );
}

export default Treenilista;