import Navi from "./components/Navi";
import Treenilista from "./components/Treenilista";
import LisaaTreeni from "./components/LisaaTreeni";
import PoistaTreeni from "./components/PoistaTreeni";
import Tilaa from "./components/Tilaa";
import LaskeBmi from "./components/LaskeBmi";
import { BrowserRouter as Router, Route } from 'react-router-dom'
import { useState, useEffect } from 'react';


function App() {

    const [treenit, setTreenit] = useState([]);

    const tallennaTreenit = () => {
      localStorage.setItem("tulevat", JSON.stringify(treenit));
    }

    const palautaTreenit = () => {
      if (localStorage.getItem("tulevat")) {
        setTreenit(JSON.parse(localStorage.getItem("tulevat")));
      } else {
        setTreenit([]);
      }
    }

    useEffect(() => {
      palautaTreenit();
    }, []);

    useEffect(() => {
      tallennaTreenit();
    }, [treenit]); 

    return (
      <Router>
        <Navi />
        <Route path="/" exact> <Treenilista treenit={treenit} setTreenit={setTreenit} tallennaTreenit={tallennaTreenit}/> </Route>
        <Route path="/lisaa"> <LisaaTreeni treenit={treenit} setTreenit={setTreenit}/> </Route>
        <Route path="/poistatreeni/:id"> <PoistaTreeni treenit={treenit} setTreenit={setTreenit}/> </Route>
        <Route path="/bmi" component={LaskeBmi} />
        <Route path="/tilaa" component={Tilaa} />
      </Router>

    );

}

export default App;
