import { useRef, useState } from 'react';
import { Button, Container, TextField, Typography } from '@material-ui/core';
import { Link, useHistory } from 'react-router-dom';
import { DateTimePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { fi } from 'date-fns/locale';
import { v4 as uuid } from 'uuid';

function LisaaTreeni(props) {

    const [pvmaika, setPvmaika] = useState(new Date());
    const tyyppi = useRef();
    const kesto = useRef();
    const history = useHistory();

    const lisaaUusiTreeni = (e) => {

        e.preventDefault();

        let uusiTreeni = {
                                id : uuid(),
                                nimi : tyyppi.current.value,
                                kesto: kesto.current.value,
                                aika : pvmaika.getTime(),
                                suoritettu : false
                             }
    
        props.setTreenit([...props.treenit, uusiTreeni]);
        history.push("/");

    }

    return (
        <Container maxWidth="sm">
            <MuiPickersUtilsProvider utils={DateFnsUtils} locale={fi}>
            <form onSubmit={lisaaUusiTreeni}>

            <Typography variant="h6" style={{textAlign: "center", margin: "20px 0 10px 0"}}>Lisää uusi harjoitus aikatauluun:</Typography>

            <TextField 
                label="Harjoituksen tyyppi"
                variant="outlined"
                fullWidth={true}
                inputRef={tyyppi}
                style={{marginBottom : 10}}
            />

            <TextField 
                label="Harjoituksen oletuskesto minuutteina"
                variant="outlined"
                type="number"
                fullWidth={true}
                inputRef={kesto}
                style={{marginBottom : 10}}
            />

            <DateTimePicker 
                label="Suoritusajankohta"
                inputVariant="outlined"
                fullWidth={true}
                cancelLabel="Peruuta"
                ampm={false}
                value={pvmaika}
                format="d.M.y HH:mm"
                onChange={setPvmaika}
                style={{marginBottom : "20px"}}
            />

            <Button 
                type="submit"
                variant="contained"
                fullWidth
                style={{marginBottom : 10, backgroundColor: "#a775f3"}}
            >Lisää uusi harjoitus</Button>

            <Button 
                component={Link}
                to="/"
                variant="contained"
                fullWidth
                style={{backgroundColor: "#b09dd8"}}
            >Peruuta</Button>

            </form>
        </MuiPickersUtilsProvider>
        </Container>
    );
}

export default LisaaTreeni;