import { AppBar, Toolbar, CssBaseline, Container } from '@material-ui/core';
import { Link } from 'react-router-dom';

function Navi() {

    return (
        <Container maxWidth="sm">
        <CssBaseline>
            <AppBar position="sticky" color="primary">
                <Toolbar>
                    
                        <Link to="/" style={{marginLeft: "30px", marginRight: "40px", color: "white", textDecoration: "none"}}>Aloitus</Link>
                   
                        <Link to="/lisaa" style={{marginRight: "40px", color: "white", textDecoration: "none"}}>Lisää harjoitus</Link>
                
                        <Link to="/bmi" style={{marginRight: "40px", color: "white", textDecoration: "none"}}>Painoindeksilaskuri</Link>

                        <Link to="/tilaa" style={{marginRight: "30px", color: "white", textDecoration: "none"}}>Tilaa ilmainen treeniohjelma</Link>
                   
                </Toolbar>
            </AppBar>
        </CssBaseline>
        </Container>
    )
}

export default Navi;