import { Container, TextField, Typography, FormControlLabel, FormHelperText, FormControl, Radio, FormLabel, RadioGroup, Button } from "@material-ui/core";
import { useState, useRef } from 'react';

function Tilaa() {

    const lomaketiedot = useRef({});
    const [virheilmoitus, setVirheilmoitus] = useState({});

    const eventHandler = (e) => {

        e.preventDefault();
    
        let virheet = {};
    
        if (!lomaketiedot.current.etunimi) {
            virheet = {...virheet, etunimi : "Etunimi puuttuu"}
        }
    
        if (!lomaketiedot.current.sukunimi) {
          virheet = {...virheet, sukunimi : "Sukunimi puuttuu"}
        }

        // if (!lomaketiedot.current.puhnro) {
        //     virheet = {...virheet, puhnro : "Puhelinnumero puuttuu"}
        // } else {
        //     if (lomaketiedot.current.puhnro.length < 6) {
        //         virheet = {...virheet, puhnro : "Virheellinen puhelinnumero"}
        //     }
        // }

        // if (!lomaketiedot.current.email) {
        //     virheet = {...virheet, email : "Sähköpostiosoite puuttuu"}
        // } else {
        //     if (lomaketiedot.current.email.search("@") === -1) {
        //         virheet = {...virheet, email : "Virheellinen sähköpostiosoite"}
        //     }
        // }

        if (!lomaketiedot.current.puhnro && !lomaketiedot.current.email) {
            virheet = {...virheet, email: "Yhteydenottotiedot puuttuvat"}
            virheet = {...virheet, puhnro: "Yhteydenottotiedot puuttuvat"}
        }

        if (!lomaketiedot.current.ohjelma) {
            virheet = {...virheet, ohjelma : "Valitse sopiva treeniohjelma"}
        } 
      
        if (Object.entries(virheet).length > 0) {
            setVirheilmoitus({...virheet});
        } else {
            setVirheilmoitus({});
            alert("Kiitos tilauksestasi, olemme sinuun yhteydessä valitsemallasi tavalla mahdollisimman pian!") 
        }
    }

    const inputHandler = (e) => {

        let arvo = (e.target.type === "button") ? e.target.checked : e.target.value;
    
        lomaketiedot.current[e.target.name] = arvo;
    
      }

    return(

    <Container maxWidth="sm">
        <Typography style={{margin: "20px 0 20px 0", fontSize: "20px", textAlign: "center"}}>Tilaa valitsemasi ilmainen treeniohjelma</Typography>

        <form onSubmit={eventHandler}>

        <TextField 
            style={{marginBottom: "10px"}}
            name="etunimi"
            variant="outlined"
            label="Etunimi"
            fullWidth={true}
            error={Boolean(virheilmoitus.etunimi)}
            helperText={virheilmoitus.etunimi}
            onChange={ inputHandler }
        />

        <TextField 
            style={{marginBottom: "10px"}}
            name="sukunimi"
            variant="outlined"
            label="Sukunimi"
            fullWidth={true}
            error={Boolean(virheilmoitus.sukunimi)}
            helperText={virheilmoitus.sukunimi}
            onChange={ inputHandler }
        />

        <TextField 
            style={{marginBottom: "10px"}}
            name="paikkakunta"
            variant="outlined"
            label="Asuinpaikkakunta"
            fullWidth={true}
            onChange={ inputHandler }
        />

        <Typography style={{textAlign: "center", margin: "10px 0 10px 0"}}>Toimitamme treeniohjelman PDF-tiedostona joko WhatsAppiin tai sähköpostilla, valitse itsellesi sopivin tapa:</Typography>
        <TextField 
            style={{marginBottom: "10px"}}
            name="puhnro"
            variant="outlined"
            label="Puhelinnumero"
            fullWidth={true}
            error={Boolean(virheilmoitus.puhnro)}
            helperText={virheilmoitus.puhnro}
            onChange={ inputHandler }
        />

        <TextField 
            style={{marginBottom: "10px"}}
            name="email"
            variant="outlined"
            label="Sähköpostiosoite"
            fullWidth={true}
            error={Boolean(virheilmoitus.email)}
            helperText={virheilmoitus.email}
            onChange={ inputHandler }
        />

        <FormControl fullWidth style={{marginTop : 10}}>
        <FormLabel style={{marginBottom: "10px"}}>Valitse sinua kiinnostava treeniohjelma: </FormLabel>
        <RadioGroup>
          <FormControlLabel 
            value="kotitreeni" 
            label="Kotitreeniohjelma"
            control={<Radio 
                        name="ohjelma"  
                        size="small"
                        onChange={ inputHandler } 
                    />} 
        />
        
        <FormControlLabel 
            value="saliohjelma" 
            label="Kuntosaliohjelma aloittelevalle"
            control={<Radio 
                        name="ohjelma"  
                        size="small"
                        onChange={ inputHandler } 
                    />} 
        />

        <FormControlLabel 
            value="saliohjelmakokemus" 
            label="Kuntosaliohjelma kokeneelle"
            control={<Radio 
                        name="ohjelma"  
                        size="small"
                        onChange={ inputHandler } 
                    />} 
        />

        </RadioGroup>
        <FormHelperText error={Boolean(virheilmoitus.ohjelma)}>{virheilmoitus.ohjelma}</FormHelperText>      
        </FormControl>
      
        <Button 
            type="submit"
            variant="contained" 
            color="primary"
            fullWidth={true}
            size="large"
            style={{margin: "10px 0 10px 0", backgroundColor: "#a775f3"}}
        >
        Tilaa treeniohjelma!
        </Button>


        </form>
    </Container>
    );
    
}

export default Tilaa;