import { Container, TextField, Typography, Button } from '@material-ui/core';
import { useState } from 'react';

function LaskeBmi() {

    const [pituus, setPituus] = useState();
    const [paino, setPaino] = useState();
    const [painoindeksi, setPainoindeksi] = useState();
    const [ilmoitus, setIlmoitus] = useState();

    const laskePainoindeksi = () => {
        const pituusM = pituus / 100;
        const painoindeksi = (paino / pituusM / pituusM).toFixed(2);
        setPainoindeksi(painoindeksi)
    
        if (painoindeksi > 40) {
        setIlmoitus(`Painoindeksisi on ${painoindeksi}. Sairaalloinen ylipaino.`);
        }
        else if (painoindeksi >= 35) {
        setIlmoitus(`Painoindeksisi on ${painoindeksi}. Vaikea ylipaino.`);
        }
        else if (painoindeksi >= 30) {
        setIlmoitus(`Painoindeksisi on ${painoindeksi}. Merkittävä ylipaino.`);
        }
        else if (painoindeksi >= 25) {
        setIlmoitus(`Painoindeksisi on ${painoindeksi}. Lievä ylipaino.`);
        }
        else if (painoindeksi >= 18.5) {
        setIlmoitus(`Painoindeksisi on ${painoindeksi}. Normaali paino.`);
        } 
        else if (painoindeksi >= 17) {
        setIlmoitus(`Painoindeksisi on ${painoindeksi}. Normaalia alhaisempi paino.`);
        }
        else if (painoindeksi >= 15) {
        setIlmoitus(`Painoindeksisi on ${painoindeksi}. Merkittävä alipaino.`);
        }
        else if (painoindeksi < 15) {
        setIlmoitus(`Painoindeksisi on ${painoindeksi}. Sairaalloinen alipaino.`);
        }
    }

    return (
        <Container maxWidth="sm">
        <Typography variant="h6" style={{margin: "20px 0 20px 0", textAlign: "center"}}>Painoindeksilaskuri</Typography>

        <TextField  
            type="number" 
            label="Anna pituutesi senttimetreinä (cm)"
            variant="outlined"
            fullWidth
            style={{marginBottom: "10px"}}
            onChange={ (e) => {
                                setPituus(e.target.value)     
                                }
            } 
        />
        <TextField 
            type="number" 
            label="Anna painosi kilogrammoina (kg)"
            variant="outlined"
            style={{marginBottom: "20px"}}
            fullWidth
            onChange={ (e) => {
                                setPaino(e.target.value)
                                }
            }
        />

        <Button variant="contained"
                    color="primary"
                    fullWidth
                    size="large"
                    style={{marginBottom : 10, backgroundColor: "#a775f3"}} 
                    onClick={laskePainoindeksi}
        > Laske painoindeksisi</Button>

        <Typography style={{textAlign: "center"}}>{ilmoitus}</Typography>
      
    </Container>
    );
}

export default LaskeBmi;
